import React from "react"
import { Link } from "react-router-dom";

export default class About extends React.Component {
    render() {
        return (
            <div className='std-page center-page-x center'>
                <div className='centered-page-box'>
                    <h1>About Ezyvue</h1>
                    <div>
                        <p>
                            Ezyvue specializes in producing 3D, AR and VR visualization applications for the web.
                        </p>
                        <h2>Products</h2>
                        <h3>Ezyvue 3D Viewer</h3>
                        <p>
                            The 3D Viewer can be embedded on web pages to display photo-realistic models in 3D, AR and VR.
                            It has many features to enhance the user experience including 3D annotations, animation triggers and product customization.
                        </p>
                        <h3>Ezyvue Studio</h3>
                        <p>
                            Ezyvue Studio is a comprehensive online editor that allows users to customize various aspects of 3D scenes.
                        </p>
                        <h4>Demos</h4>
                        <p>
                            View the <Link to='/demos'>demos</Link> to see how 3D products can look on your website.
                        </p>
                    </div>
                </div>
            </div>
        );
    }
}